<template>
  <v-data-table
    v-model="selected"
    :headers="headers"
    :items="minuman"
    :search="search"
    :single-select="singleSelect"
    sort-by="calories"
    item-key="name"
    show-select
    class="elevation-1"
  >
    <template v-slot:top>
       <v-switch v-model="singleSelect" label="Single select" class="pa-3"></v-switch>
      <v-toolbar flat color="white">
        <v-toolbar-title>My CRUD</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
          <v-spacer></v-spacer>
          <v-text-field
          v-model="search"
          append-icon="mdi-search-web"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="minuman name"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                      <v-overflow-btn
                        class="my-2"
                        :items="dropdown_font"
                        label="Calories"
                        v-model="editedItem.calories"
                        ></v-overflow-btn>
                    <!-- <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field> -->
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.action="{ item }">
      <v-icon
        class="mr-1"
        @click="editItem(item)"
      >
        mdi-silverware
      </v-icon>
      <v-icon
      class="mr-1"
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    <!-- <template v-slot:no-data>
      <v-btn color="primary" @click="initialize">Reset</v-btn>
    </template> -->
  </v-data-table>
</template>

<script>
import { minuman } from '../firebase'
  export default {
    data: () => ({
      singleSelect: false,
      selected: [],
    dropdown_font: ['Es', 'Hangat'],
      cek : -1,
      dialog: false,
      search: '',
      headers: [
        {
          text: 'Minuman (100g serving)',
          align: 'left',
          sortable: false,
          value: 'name',
        },
        { text: 'Calories', value: 'calories' },
        { text: 'Fat (g)', value: 'fat' },
        { text: 'Carbs (g)', value: 'carbs' },
        { text: 'Protein (g)', value: 'protein' },
        { text: 'Actions', value: 'action', sortable: false },
      ],
      minuman: [],
      //desserts1: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
      defaultItem: {
        name: '',
        calories: 0,
        fat: 0,
        carbs: 0,
        protein: 0,
      },
    }),
    firebase: {
      minuman : minuman,
   
    },
    computed: {
      formTitle () {
        return this.cek === -1 ? 'New Item' : 'Edit Item'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
    },
    // created () {
    //   this.initialize()
    // },
    methods: { 
      editItem (item) {
        this.editedIndex = item['.key']
        this.cek = 0
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteItem (item) {
        //const index = this.desserts.indexOf(item)
        confirm('Are you sure you want to delete this item?') && minuman.child((item['.key'])).remove()
      },
      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },
      save () {
         if (this.cek > -1){
           minuman.child(this.editedIndex).set({name: this.editedItem.name,calories: 
           this.editedItem.calories,
           fat : this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein
           })
           this.clear()
         } else {
           minuman.push({name: this.editedItem.name, calories: this.editedItem.calories,
           fat : this.editedItem.fat, carbs : this.editedItem.carbs, protein: this.editedItem.protein
           })
           this.clear()
         }
         this.close()
        //  Hati-hati dengan tutup kurung, fungsinya tidak akan berfungsi jika tutup kurangnya tidak ada
      },
      clear() {
        this.editedItem={}
        this.editedItem.name= ''
        this.editedItem.calories= 0
        this.editedItem.fat= 0
        this.editedItem.carbs= 0
        this.editedItem.protein= 0
        this.cek=-1
      },
    },
  }
</script>
